  margin-left: 0;
}

.btn-breadcrumb .btn.btn-default:after {
  border-left: 10px solid #f3f5fb;
  border-right: 0;
}

.btn-breadcrumb .btn.btn-default:hover:after {
  border-left: 10px solid #f3f5fb;
  border-right: 0;
}

.btn-breadcrumb .btn.btn-default:hover:before {
  border-left: 10px solid #e8edf2;
  border-right: 0;
}

.btn-breadcrumb a.btn.btn-default.btn-active:hover:after {
  border-left: 10px solid #219289;
  border-right: 0;
}

.btn-breadcrumb a.btn.btn-default.btn-active:hover:before {
  border-left: 10px solid #219289;
  border-right: 0;
}

.btn-breadcrumb a.btn.btn-default.btn-active:after {
